import React, {Component} from 'react';
import { Input, Icon, Button } from 'antd';
import axios from 'src/config/axios';
import {Link} from "react-router-dom";
import './SignUp.scss'

interface ISignUpState {
  account: string,
  password: string,
  passwordConfirmation: string
}

class SignUp extends Component<any, ISignUpState> {
  constructor(props){
    super(props)
    this.state = {
      account: '',
      password: '',
      passwordConfirmation: ''
    }
    this.submit = this.submit.bind(this)
  }

  onChange = (key, value) => {
    const newState = {}
    newState[key] = value
    this.setState(newState)
  }
  submit = async () => {
    const { account, password, passwordConfirmation } = this.state
    try{
      await axios.post('sign_up/user', {
        account,
        password,
        password_confirmation: passwordConfirmation
      })
      this.props.history.push('/login')
    }catch (e) {
      throw new Error(e)
    }
  }
  public render() {
    // @ts-ignore
    const { account, password, passwordConfirmation } = this.state
    return (
      <div className="SignUp" id="SignUp">
        <h1>番茄闹钟 注册</h1>
        <Input
          placeholder="请输入用户名"
          prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
          value={account}
          onChange={(e) => this.onChange('account', e.target.value)}
        />
        <Input.Password placeholder="请输入密码" value={password} onChange={(e) => this.onChange('password', e.target.value)}/>
        <Input.Password placeholder="请确认密码" value={passwordConfirmation} onChange={(e) => this.onChange('passwordConfirmation', e.target.value)}/>
        <Button className="signUpButton" type="primary" onClick={this.submit}>注册</Button>
        <p>如果你有账号，请立即<Link to="/login">登录</Link></p>
      </div>
    )
  }
}

export default SignUp;



